import * as React from "react";
import styled from "styled-components";
import { textColor } from "assets/colors";

type Props = {
  status: number;
};

const ButtonLabel: React.FC<Props> = (props: Props) => {
  switch (props.status) {
    case 0:
      return <Text>마스크 사용 시작</Text>;
    case 1:
      return <Text>마스크 재사용 시작</Text>;
    case 2:
      return <Text>마스크 사용 중단</Text>;
    default:
      return <Text>마스크 사용 시작</Text>;
  }
};

const Text = styled.p`
  display: block;
  color: ${textColor};
  margin-left: 0.625rem;
  font-size: 15px;
  font-weight: 500;
  font-family: "Noto Sans KR", sans-serif;
`;

export default ButtonLabel;



