import * as React from "react";
import styled from "styled-components";
import CircleProgressBar from "../molecules/CircleProgressBar";

type Props = {};

const UsageStateSection: React.FC<Props> = (props: Props) => {
  return (
    <Container>
      <CircleProgressBar />
      <ProgressTitle>
        마스크{"\n"}
        교체할 시간!
      </ProgressTitle>
      <ProgressTime>사용시간 06:30:25</ProgressTime>
    </Container>
  );
};

const Container = styled.div`
  width: 300px;
  height: 300px;
  position: relative;
`;

const ProgressTitle = styled.p`
  text-align: center;
  position: absolute;
  font-size: 30px;
  color: #1a1b1d;
  top: 110px;
  left: 25px;
`;

const ProgressTime = styled.p`
  text-align: center;
  position: absolute;
  opacity: 0.68;
  font-size: 18px;
  top: 170px;
  left: 75px;
`;

export default UsageStateSection;



