import * as React from "react";
import styled from "styled-components";

import { autobind } from "core-decorators";
import { inject, observer } from "mobx-react";
import { Provider } from "mobx-react";
import { stores } from "./modules";
import { IMaskStore } from "./modules/store/MaskStore";
import { textColor } from "assets/colors";

import MaskButton from "./organisms/MaskButton";
import UsageStateSection from "./organisms/UsageStateSection";
import BackIconImg from "assets/img/back_icon.png";

type Props = {
  MaskStore?: IMaskStore;
};

@inject("MaskStore")
@observer
@autobind
class MaskPage extends React.Component<Props> {
  render() {
    return (
      <Container>
        <BackButton src={BackIconImg} />
        <SubContainer>
          <UsageStateSection />
          <MaskButton />
          <MaskInfoTitle>마스크가 재사용이 가능한가요?</MaskInfoTitle>
          <MaskInfoDiscription>
            측정소 기준치에 대한 세부 정보 텍스트 라인 블라블라 블라블라
            블라블라 블라블라 측정소 기준치에 대한 세부 정보 텍스트 라인
            블라블라 블라블라 블라블라 블라블라
          </MaskInfoDiscription>
        </SubContainer>
      </Container>
    );
  }
}

const Container = styled.div`
  width: 100%;
  height: 100%;
  overflow-y: scroll;
`;

const SubContainer = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 7.3125rem;
`;

const BackButton = styled.img`
  margin-left: 0.625rem;
  margin-top: 1.25rem;
  width: 36px;
  height: 36px;
`;

const MaskInfoTitle = styled.p`
  margin-top: 7.375rem;
  text-align: center;
  font-size: 14px;
  font-weight: 500;
  font-family: "Noto Sans KR", sans-serif;
  color: ${textColor};
`;

const MaskInfoDiscription = styled.p`
  margin-top: 0.9375rem;
  margin-bottom: 1.625rem;
  text-align: center;
  color: ${textColor};
  font-size: 10px;
  font-weight: 500;
  font-family: "Noto Sans KR", sans-serif;
  padding-left: 1.875rem;
  padding-right: 1.875rem;
`;

class MaskPageWithStore extends React.Component<Props> {
  public render() {
    return (
      <Provider {...stores}>
        <MaskPage />
      </Provider>
    );
  }
}

export default MaskPageWithStore;



