import React from "react";
import styled from "styled-components";
import { autobind } from "core-decorators";

import WeatherCard from "./WeatherCard";

type Props = {
  onScrollAction: (currentScrollPos: number) => void;
  phase: 1 | 2 | 3 | 4;
};

@autobind
class CardSection extends React.Component<Props> {
  private containerRef = React.createRef<any>();

  constructor(props: Props) {
    super(props);
  }

  render() {
    return (
      <Container ref={this.containerRef}>
        <WeatherCard
          isCurrentLocation={true}
          location={"서울시 관악구"}
          dustState={"나쁨"}
          fineDust={50}
          ultrafineDust={65}
          phase={this.props.phase}
          description={"마스크가 필요한 날입니다."}
        />
        <WeatherCard
          location={"서울시 마포구"}
          dustState={"매우나쁨"}
          fineDust={50}
          ultrafineDust={125}
          phase={this.props.phase}
          description={"마스크를 꼭 착용하세요."}
        />
        <WeatherCard
          location={"서울시 마포구"}
          dustState={"매우나쁨"}
          fineDust={50}
          ultrafineDust={125}
          phase={this.props.phase}
          description={"마스크를 꼭 착용하세요."}
        />
      </Container>
    );
  }

  componentDidMount() {
    this.onScrollEvent();
  }

  componentWillUnmount() {
    this.offScrollEvent();
  }

  onScrollEvent() {
    this.containerRef.current!.addEventListener(
      "scroll",
      this.onScrollAction,
      false
    );
  }

  offScrollEvent() {
    this.containerRef.current!.removeEventListener(
      "scroll",
      this.onScrollAction,
      false
    );
  }

  onScrollAction() {
    const currentScrollPos = this.containerRef.current!.scrollTop;
    this.props.onScrollAction(currentScrollPos);
  }
}

const Container = styled.div`
  display: grid;
  grid-row-gap: 0.625rem;
  width: calc(100% - 1.25rem);
  height: calc(100% - 8.4375rem);
  margin: 8.4375rem 0.625rem 6.25rem 0.625rem;
  overflow-y: scroll;
  padding-top: 8.4375rem;
  padding-bottom: 8.4375rem;
`;

export default CardSection;



