import * as React from "react";
import styled from "styled-components";

type Props = {
  status: number;
};

const ButtonIcon: React.FC<Props> = (props: Props) => {
  switch (props.status) {
    case 0:
      return <Icon />;
    case 1:
      return <Icon />;
    case 2:
      return <Icon />;
    default:
      return <Icon />;
  }
};

const Icon = styled.div`
  width: 15px;
  height: 15px;
  background-color: #000000;
`;

export default ButtonIcon;



